import { useState } from "react";
import QvipleLoader from "../../../Loader/QvipleLoader";
import { assestsFeedUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import dayjs from "dayjs";
import { localTimeFormat } from "../../../Utils/Functions/localTime";
import { imageShowUrl, pdfCdnUrl } from "../../../Services/UrlConfig/BaseUrl";
import LmsLeaveForDate from "./LmsLeaveForDate";
import style from "../LmsManager.module.css";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useLmsLeaveRequestAction } from "../../../Hooks/ApiHooks/LmsManager/lms-manager-api";
import { useTranslation } from "react-i18next";
const LmsLeaveRequestCard = ({ leave, onRefetch }) => {
  const { t } = useTranslation();
  const [lmsLeaveRequestAction] = useLmsLeaveRequestAction();
  const [disabled, setDisabled] = useState(false);
  const [open, setOpen] = useState(false);

  const onLeaveAction = (query) => {
    if (leave?._id) {
      setDisabled((pre) => !pre);
      lmsLeaveRequestAction({
        lid: leave?._id,
        requestLeave: { status: query },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onToggle = () => {
    setOpen((pre) => !pre);
  };
  return (
    <>
      <div className={style.lmsr_card}>
        <div className={style.lmsrc_card}>
          <div className={style.lmsrc_card_inner}>
            <img
              src={
                leave?.staff?.staffProfilePhoto
                  ? `${imageShowUrl}/${leave?.staff?.staffProfilePhoto}`
                  : `${assestsFeedUrl}/feed-user-avatar.svg`
              }
              alt="staff icon"
            />
            <div className={style.lmsrci_text}>
              <h6>{`${leave?.staff?.staffFirstName ?? ""} ${
                leave?.staff?.staffMiddleName
                  ? leave?.staff?.staffMiddleName
                  : ""
              } ${leave?.staff?.staffLastName ?? ""}`}</h6>
              <p>
                {t("raised_on")}
                {localTimeFormat(leave?.createdAt, "LT")}
                {", "}
                {dayjs(leave?.createdAt).format("DD MMM YYYY")}
                {", "}
                <span className={style.lmsrc_date} onClick={onToggle}>
                  {t("for_dates")}
                </span>
              </p>
            </div>
          </div>
          <div className={style.lmsrc_btn}>
            <CreateButton
              label={t("reject")}
              onAction={() => onLeaveAction("Rejected")}
              customStyle={rejectCustomStyle}
            />
            <CreateButton
              label={t("approve")}
              onAction={() => onLeaveAction("Accepted")}
              customStyle={approveCustomStyle}
            />
          </div>
        </div>
        <div
          className={style.lmsrci_text}
          style={{
            marginTop: "0.5rem",
          }}
        >
          <h6
            style={{
              fontWeight: "400",
            }}
          >
            {t("leave_type")} {" : "}
            {leave?.leave_type}
          </h6>
        </div>
        <div
          className={style.lmsrci_text}
          style={{
            marginTop: "0.5rem",
          }}
        >
          <h6
            style={{
              fontWeight: "400",
              whiteSpace: "break-spaces",
            }}
          >
            {t("reason")} {" : "}
            {leave?.reason}
          </h6>
        </div>
        {leave?.attach && (
          <div
            className={style.lmsrci_text}
            style={{
              marginTop: "0.5rem",
            }}
          >
            <h6
              style={{
                fontWeight: "400",
                whiteSpace: "break-spaces",
              }}
            >
              {t("attachment")} {" : "}
              <a
                href={`${pdfCdnUrl}/${leave?.attach}`}
                target="_blank"
                rel="noopener noreferrer"
                style={{
                  textDecoration: "none",
                }}
              >
                <span className={style.lmsrc_date}>{t("view_attachment")}</span>
              </a>
            </h6>
          </div>
        )}
      </div>
      {open && <LmsLeaveForDate onClose={onToggle} allDate={leave?.date} />}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default LmsLeaveRequestCard;

const rejectCustomStyle = {
  "--bg-button": "#e7e7e7",
  "--color-button": "#6f727c",
  "--color-white": "#ffffff",
  margin: "0",
  padding: "0.2rem 0.8rem",
};
const approveCustomStyle = {
  margin: "0",
  padding: "0.2rem 0.8rem",
};
